import { Link } from '../routes'

const Home = () => {
    const divStyle = {
        textAlign : "center",
    },
    buttonStyle = {
        margin : "5px"
    }
    return <div style={divStyle}>
        <button style={buttonStyle}>Go to about page</button>
        <button style={buttonStyle}>Go to contact page</button>
        <button style={buttonStyle}>Go to user List</button>
        {/* <Link style={linkStyle} route="/about">Go to about page</Link>
        <Link style={linkStyle} route="/contact">Go to contact page</Link>
        <Link style={linkStyle} route="/user">Go to user List</Link> */}
    </div>
}

export default Home